<template>
  <view class="tab-bar-container">
    <!-- ÊØõÁéªÁíÉËÉåÊôØÂ±Ç -->
    <view class="tab-bar-backdrop"></view>
    
    <!-- ‰∏ªË¶ÅÂÜÖÂÆπÂå∫ -->
    <view class="tab-bar">
      <view 
        v-for="(item, index) in tabList" 
        :key="index"
        class="tab-item"
        :class="{ 
          active: currentPath === item.pagePath,
          'tab-item-loading': iconLoadingStates[index]
        }"
        @click="switchTab(item.pagePath, index)"
        @touchstart="onTouchStart(index)"
        @touchend="onTouchEnd(index)"
      >
        <!-- Ê¥ªË∑ÉÁä∂ÊÄÅËÉåÊôØÊ≥¢Á∫π -->
        <view 
          class="tab-ripple" 
          :class="{ active: currentPath === item.pagePath }"
        ></view>
        
        <!-- ÂõæÊ†áÂÆπÂô® -->
        <view class="tab-icon-container">
          <!-- ‰∏ªÂõæÊ†á -->
          <image 
            v-if="!iconErrors[index]"
            :src="getTabIconUrl(item, currentPath === item.pagePath)" 
            class="tab-icon"
            :class="{ 
              active: currentPath === item.pagePath,
              bounce: animatingIndex === index 
            }"
            @load="onIconLoad(index)"
            @error="onIconError(index)"
            mode="aspectFit"
          />
          
          <!-- Â§áÁî®ÂõæÊ†á - ‰ΩøÁî®UnicodeÂ≠óÁ¨¶ -->
          <view 
            v-else
            class="tab-icon-fallback"
            :class="{ active: currentPath === item.pagePath }"
          >
            {{ item.fallbackIcon }}
          </view>
          
          <!-- Ê¥ªË∑ÉÁä∂ÊÄÅÊåáÁ§∫ÁÇπ -->
          <view 
            class="active-indicator"
            :class="{ show: currentPath === item.pagePath }"
          ></view>
        </view>
        
        <!-- ÊñáÊú¨Ê†áÁ≠æ -->
        <text 
          class="tab-text"
          :class="{ active: currentPath === item.pagePath }"
        >
          {{ item.text }}
        </text>
        
        <!-- Á∫¢ÁÇπÂæΩÁ´†ÔºàÂèØÈÄâÂäüËÉΩÔºâ -->
        <view 
          v-if="item.badge && item.badge > 0"
          class="tab-badge"
        >
          {{ item.badge > 99 ? '99+' : item.badge }}
        </view>
      </view>
    </view>
    
    <!-- Â∫ïÈÉ®ÂÆâÂÖ®Âå∫Âüü -->
    <view class="safe-area-bottom"></view>
  </view>
</template>

<script setup>
import { ref, reactive, onMounted, nextTick } from 'vue';

// ========== ÂìçÂ∫îÂºèÊï∞ÊçÆ ==========
const currentPath = ref('/pages/HomePage/HomePage');
const iconErrors = reactive({}); // ËÆ∞ÂΩïÂõæÊ†áÂä†ËΩΩÈîôËØØÁä∂ÊÄÅ
const iconLoadingStates = reactive({}); // ËÆ∞ÂΩïÂõæÊ†áÂä†ËΩΩÁä∂ÊÄÅ
const animatingIndex = ref(-1); // ÂΩìÂâçÊâßË°åÂä®ÁîªÁöÑÁ¥¢Âºï

// OSSÈÖçÁΩÆ - ‰ºòÂåñÈÖçÁΩÆÈ°π
const ossConfig = {
  baseUrl: 'https://birdfront-oss.oss-cn-shanghai.aliyuncs.com',
  // Ê∑ªÂä†Â§áÁî®CDNÂüüÂêç
  fallbackUrls: [
    'https://birdfront-oss.oss-cn-hangzhou.aliyuncs.com',
    'https://birdfront-oss.oss-cn-beijing.aliyuncs.com'
  ],
  timeout: 5000 // Ë∂ÖÊó∂Êó∂Èó¥
};

// ========== OSSÂ∑•ÂÖ∑ÊñπÊ≥ï ==========
/**
 * Ëé∑ÂèñOSSÂõæÁâáURL
 * @param {string} filename - Êñá‰ª∂Âêç
 * @param {string} size - Â∞∫ÂØ∏Á±ªÂûã
 * @param {number} fallbackIndex - Â§áÁî®URLÁ¥¢Âºï
 * @returns {string} ÂÆåÊï¥ÁöÑOSS URL
 */
const getOSSUrl = (filename, size = 'icon', fallbackIndex = 0) => {
  if (!filename) return '';
  
  // Á°Æ‰øùÊñá‰ª∂Âêç‰∏ç‰ª•ÊñúÊù†ÂºÄÂ§¥
  const cleanFilename = filename.startsWith('/') ? filename.slice(1) : filename;
  
  // Ê†πÊçÆÂ∞∫ÂØ∏Á±ªÂûãËÆæÁΩÆ‰∏çÂêåÁöÑÂ§ÑÁêÜÂèÇÊï∞ - ‰ºòÂåñÂèÇÊï∞
  let params = '';
  switch(size) {
    case 'icon':
      params = '?x-oss-process=image/resize,m_lfit,w_96,h_96/quality,q_95/format,webp';
      break;
    case 'small-icon':
      params = '?x-oss-process=image/resize,m_lfit,w_64,h_64/quality,q_95/format,webp';
      break;
    case 'large-icon':
      params = '?x-oss-process=image/resize,m_lfit,w_128,h_128/quality,q_95/format,webp';
      break;
    default:
      params = '?x-oss-process=image/resize,m_lfit,w_96,h_96/quality,q_95/format,webp';
  }
  
  // ÈÄâÊã©Âü∫Á°ÄURL
  const baseUrl = fallbackIndex < ossConfig.fallbackUrls.length 
    ? ossConfig.fallbackUrls[fallbackIndex] 
    : ossConfig.baseUrl;
  
  return `${baseUrl}/${cleanFilename}${params}`;
};

// ========== Ê†áÁ≠æÈÖçÁΩÆ - Â¢ûÂº∫ÈÖçÁΩÆ ==========
const tabList = [
  {
    pagePath: '/pages/HomePage/HomePage',
    text: 'È¶ñÈ°µ',
    iconPath: 'static/icons/home.png',
    selectedIconPath: 'static/icons/home-active.png',
    fallbackIcon: 'üè†', // UnicodeÂ§áÁî®ÂõæÊ†á
    badge: 0 // ÂæΩÁ´†Êï∞Èáè
  },
  {
    pagePath: '/pages/MapPage/MapPage',
    text: 'Âú∞Âõæ',
    iconPath: 'static/icons/map.png',
    selectedIconPath: 'static/icons/map-active.png',
    fallbackIcon: 'üó∫Ô∏è',
    badge: 0
  },
  {
    pagePath: '/pages/UploadPage/UploadPage',
    text: '‰∏ä‰º†',
    iconPath: 'static/icons/upload.png',
    selectedIconPath: 'static/icons/upload-active.png',
    fallbackIcon: 'üì§',
    badge: 0
  },
  {
    pagePath: '/pages/ProfilePage/ProfilePage',
    text: 'ÊàëÁöÑ',
    iconPath: 'static/icons/profile.png',
    selectedIconPath: 'static/icons/profile-active.png',
    fallbackIcon: 'üë§',
    badge: 0
  }
];

// ========== ËÆ°ÁÆóÂ±ûÊÄßÂíåÊñπÊ≥ï ==========

/**
 * Ëé∑ÂèñÊ†áÁ≠æÂõæÊ†áÁöÑOSS URL - Â¢ûÂº∫ÁâàÊú¨
 * @param {Object} item - Ê†áÁ≠æÈ°πÂØπË±°
 * @param {Boolean} isActive - ÊòØÂê¶‰∏∫ÊøÄÊ¥ªÁä∂ÊÄÅ
 * @returns {string} ÂõæÊ†áÁöÑOSS URL
 */
const getTabIconUrl = (item, isActive) => {
  const iconPath = isActive ? item.selectedIconPath : item.iconPath;
  return getOSSUrl(iconPath, 'icon');
};

// ========== ‰∫ã‰ª∂Â§ÑÁêÜÂáΩÊï∞ ==========

/**
 * ÂàáÊç¢Ê†áÁ≠æÈ°µ - Â¢ûÂº∫ÁâàÊú¨
 * @param {string} path - È°µÈù¢Ë∑ØÂæÑ
 * @param {number} index - Ê†áÁ≠æÁ¥¢Âºï
 */
const switchTab = async (path, index) => {
  // Èò≤Ê≠¢ÈáçÂ§çÁÇπÂáª
  if (currentPath.value === path) return;
  
  // ÊâßË°åÁÇπÂáªÂä®Áîª
  triggerClickAnimation(index);
  
  // Êõ¥Êñ∞ÂΩìÂâçË∑ØÂæÑ
  currentPath.value = path;
  
  // Ê∑ªÂä†Ëß¶ËßâÂèçÈ¶à - Â¢ûÂº∫ÂèçÈ¶à
  try {
    uni.vibrateShort({
      type: 'light'
    });
  } catch (error) {
    console.warn('Ëß¶ËßâÂèçÈ¶à‰∏çÊîØÊåÅ:', error);
  }
  
  // Ë∑≥ËΩ¨È°µÈù¢ - Â¢ûÂº∫ÈîôËØØÂ§ÑÁêÜ
  try {
    await new Promise((resolve, reject) => {
      uni.switchTab({
        url: path,
        success: () => {
          console.log('ÂàáÊç¢Âà∞:', path);
          resolve();
        },
        fail: (error) => {
          console.error('È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•:', error);
          reject(error);
        }
      });
    });
  } catch (error) {
    // ÈôçÁ∫ßÊñπÊ°àÔºö‰ΩøÁî®navigateTo
    try {
      await new Promise((resolve, reject) => {
        uni.navigateTo({
          url: path,
          success: resolve,
          fail: reject
        });
      });
    } catch (navError) {
      console.error('ÂØºËà™Â§±Ë¥•:', navError);
      uni.showToast({
        title: 'È°µÈù¢Ë∑≥ËΩ¨Â§±Ë¥•',
        icon: 'none',
        duration: 2000
      });
    }
  }
};

/**
 * Ëß¶ÂèëÁÇπÂáªÂä®Áîª
 * @param {number} index - Ê†áÁ≠æÁ¥¢Âºï
 */
const triggerClickAnimation = (index) => {
  animatingIndex.value = index;
  setTimeout(() => {
    animatingIndex.value = -1;
  }, 300);
};

/**
 * Ëß¶Êë∏ÂºÄÂßã‰∫ã‰ª∂
 * @param {number} index - Ê†áÁ≠æÁ¥¢Âºï
 */
const onTouchStart = (index) => {
  // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†Ëß¶Êë∏ÂºÄÂßãÁöÑËßÜËßâÂèçÈ¶à
};

/**
 * Ëß¶Êë∏ÁªìÊùü‰∫ã‰ª∂
 * @param {number} index - Ê†áÁ≠æÁ¥¢Âºï
 */
const onTouchEnd = (index) => {
  // Ëß¶Êë∏ÁªìÊùüÂêéÁöÑÂ§ÑÁêÜ
};

/**
 * ÂõæÊ†áÂä†ËΩΩÊàêÂäüÂ§ÑÁêÜ
 * @param {number} index - ÂõæÊ†áÁ¥¢Âºï
 */
const onIconLoad = (index) => {
  iconLoadingStates[index] = false;
  iconErrors[index] = false;
  console.log(`ÂõæÊ†á ${index} Âä†ËΩΩÊàêÂäü`);
};

/**
 * ÂõæÊ†áÂä†ËΩΩÂ§±Ë¥•Â§ÑÁêÜ - Â¢ûÂº∫ÁâàÊú¨
 * @param {number} index - ÂõæÊ†áÁ¥¢Âºï
 */
const onIconError = (index) => {
  iconLoadingStates[index] = false;
  iconErrors[index] = true;
  console.warn(`Ê†áÁ≠æÊ†èÂõæÊ†á ${index} Âä†ËΩΩÂ§±Ë¥•Ôºå‰ΩøÁî®Â§áÁî®ÂõæÊ†á`);
  
  // ÂèØ‰ª•Â∞ùËØïÂä†ËΩΩÂ§áÁî®URLÔºàÂ¶ÇÊûúÈúÄË¶ÅÁöÑËØùÔºâ
  // ËøôÈáåÂÖà‰ΩøÁî®UnicodeÂ≠óÁ¨¶‰Ωú‰∏∫Â§áÁî®ÊñπÊ°à
};

/**
 * ÂàùÂßãÂåñÂΩìÂâçÈ°µÈù¢Ë∑ØÂæÑ - Â¢ûÂº∫ÁâàÊú¨
 */
const initCurrentPath = () => {
  try {
    // Âú®uni-app‰∏≠Ëé∑ÂèñÂΩìÂâçÈ°µÈù¢Ê†à
    const pages = getCurrentPages();
    if (pages && pages.length > 0) {
      const currentPage = pages[pages.length - 1];
      const route = currentPage.route;
      
      // Ê†πÊçÆË∑ØÁî±ÂåπÈÖçÊ†áÁ≠æ
      const matchedTab = tabList.find(tab => {
        const tabRoute = tab.pagePath.replace(/^\//, '');
        return route === tabRoute || route.includes(tabRoute);
      });
      
      if (matchedTab) {
        currentPath.value = matchedTab.pagePath;
        console.log('ÂåπÈÖçÂà∞ÂΩìÂâçÈ°µÈù¢:', matchedTab.text);
      }
    }
  } catch (error) {
    console.warn('Ëé∑ÂèñÂΩìÂâçÈ°µÈù¢Ë∑ØÂæÑÂ§±Ë¥•:', error);
    // ËÆæÁΩÆÈªòËÆ§Ë∑ØÂæÑ
    currentPath.value = '/pages/HomePage/HomePage';
  }
};

/**
 * È¢ÑÂä†ËΩΩÂõæÊ†á
 */
const preloadIcons = () => {
  tabList.forEach((item, index) => {
    iconLoadingStates[index] = true;
    
    // È¢ÑÂä†ËΩΩÊôÆÈÄöÁä∂ÊÄÅÂõæÊ†á
    const normalImg = new Image();
    normalImg.onload = () => onIconLoad(index);
    normalImg.onerror = () => onIconError(index);
    normalImg.src = getOSSUrl(item.iconPath, 'icon');
    
    // È¢ÑÂä†ËΩΩÊøÄÊ¥ªÁä∂ÊÄÅÂõæÊ†á
    const activeImg = new Image();
    activeImg.src = getOSSUrl(item.selectedIconPath, 'icon');
  });
};

// ========== ÁîüÂëΩÂë®Êúü ==========
onMounted(async () => {
  // ÂàùÂßãÂåñÂΩìÂâçË∑ØÂæÑ
  initCurrentPath();
  
  // È¢ÑÂä†ËΩΩÂõæÊ†á
  await nextTick();
  preloadIcons();
  
  console.log('TabBarÁªÑ‰ª∂Â∑≤ÊåÇËΩΩÔºåÂΩìÂâçË∑ØÂæÑ:', currentPath.value);
  
  // ÁõëÂê¨È°µÈù¢ÂèòÂåñÔºàÂ¶ÇÊûúÈúÄË¶ÅÁöÑËØùÔºâ
  // Ê≥®ÊÑèÔºöËøôÈáåÂèØ‰ª•Ê∑ªÂä†ÂÖ®Â±ÄÈ°µÈù¢ÁõëÂê¨ÈÄªËæë
});
</script>

